import Link from "next/link";

export default function Testcases(){

    return(
        <div className="flex flex-col items-center justify-center gap-10 p-8">
            <h1 className="text-white tracking-widest uppercase text-3xl font-bold">CREATE YOUR OWN TESTCASES</h1>
            <div className="form-container relative flex w-full max-w-7xl flex-col items-center justify-center rounded-2xl bg-white/5 p-8 shadow-2xl backdrop-blur-lg backdrop-filter sm:p-12">
            <div 
                className="absolute inset-0 z-0 h-full w-full rounded-2xl pointer-events-none" 
          style={{
            background: 'radial-gradient(circle at 50% 50%, #ff0000 0%, transparent 70%)',
            filter: 'blur(100px) opacity(0.4)',
            transition: 'transform 0.5s ease-in-out',
            transform: 'scale(1.2)'
                }}
            ></div>
            <form className="space-y-6 p-8 rounded-lg shadow-2xl relative overflow-hidden">
    <input
        type="text"
        placeholder="Endpoint URL (e.g., /api/login,/api/url)"
        className="w-full rounded-lg border-b-2 border-white bg-transparent py-4 text-sm text-white placeholder-white/80 transition-colors duration-300 focus:border-white focus:outline-none focus:ring-1 focus:ring-white/80"
        required
    />

    <div className="flex flex-row gap-5 justify-between w-full">
    <select
        className="w-full rounded-lg border-b-2 border-white bg-transparent py-4 text-sm text-white placeholder-white/80 transition-colors duration-300 focus:border-white focus:outline-none focus:ring-1 focus:ring-white/80"
        required
    >
        <option value="" className="bg-gray-800 text-gray-200">Select HTTP Method</option>
        <option value="GET" className="bg-gray-800 text-gray-200">GET</option>
        <option value="POST" className="bg-gray-800 text-gray-200">POST</option>
        <option value="PUT" className="bg-gray-800 text-gray-200">PUT</option>
        <option value="DELETE" className="bg-gray-800 text-gray-200">DELETE</option>
        <option value="PATCH" className="bg-gray-800 text-gray-200">PATCH</option>
    </select>
    <input
        type="number"
        placeholder="Expected Status Code (e.g., 200)"
        className="w-full rounded-lg border-b-2 border-white bg-transparent py-4 text-sm text-white placeholder-white/80 transition-colors duration-300 focus:border-white focus:outline-none focus:ring-1 focus:ring-white/80"
    />
    </div>
    <textarea
        placeholder={`Headers (JSON format)\n{ "Authorization": "Bearer token", "Content-Type": "application/json" }`}
        className="w-full rounded-lg border-b-2 border-white bg-gray-800 py-4 text-sm text-white placeholder-white/80 transition-colors duration-300 focus:border-white focus:outline-none focus:ring-1 focus:ring-white/80 h-32"
    />
    <textarea
        placeholder={`Request Body (JSON format)\n{ "name": "Milan" }`}
        className="w-full rounded-lg border-b-2 border-white bg-gray-800 py-4 text-sm text-white placeholder-white/80 transition-colors duration-300 focus:border-white focus:outline-none focus:ring-1 focus:ring-white/80 h-32"
    />
    <button
        type="submit"
        className="w-full py-3 bg-white text-black uppercase tracking-widest rounded-full hover:from-gray-700 hover:to-gray-500 focus:outline-none focus:ring-2 focus:ring-red-500 focus:ring-opacity-75 transition duration-300 ease-in-out text-xl font-bold transform hover:scale-105 active:scale-100 glow-on-hover relative z-10"
    >
        Create Test Case
    </button>
    <Link href='/dashboard/projects/testcases/testcases_overview'>
    <p className="text-sm text-blue-500 text-center">Use pre-defined generic tests</p>
    </Link>
</form>
</div>
        </div>
    )
}